.Header{
    display: flex;
    flex-direction: row;
    background-color: white;
    width: 100vw;
    height: 32px;
    margin: 0;
    padding: 0;
    justify-content: space-between;
}
.HeaderLinks{
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    justify-items: flex-end;
}
.customLinks{
    color:black;
    padding-left: 10px;
    padding-right: 20px;
    text-decoration: none;
    cursor:pointer;
    font-size: 18px;
    transition: color 0.3s ease-in;
}
.customLinks:hover{
    color: orangered;
}